import React from "react";
import { Link } from "react-router-dom";
import { Panel, PanelHeader } from "../../../components/panel/panel.jsx";
import {
  ShowModalKodeTransaksi,
  showModal,
  getParamterTransaksi,
  hideModal,
  getMasterToko,
} from "../../../actions/datamaster_action.jsx";
import { connect } from "react-redux";
import Swal from "sweetalert2";
import {
  NotifError,
  NotifSucces,
  ToastNotification,
} from "../../../components/helpers/notification.jsx";
import {
  deleteData,
  postData,
  postParameterHarga,
  putData,
  putParameterHarga,
} from "../../../config/axios.jsx";
import { reset } from "redux-form";
import { ErrorHandling } from "../../../components/helpers/function.jsx";
import Tabel from "../../../components/helpers/tabel.jsx";
import ModalGlobal from "../../../components/helpers/ModalGlobal.jsx";
import FormModalKodeTransaksi from "./FormModalKodeTransaksi.jsx";

const maptostate = (state) => {
  return {
    DataTransaksi: state.datamaster.getParamterTransaksi,
    hideModal: state.datamaster.modalDialog,
    getMasterToko: state.datamaster.getMasterToko,
  };
};

const HapusDataKodeTransaksi = (params, dispatch, listToko) => {
  Swal.fire({
    // title: "Anda Yakin !!",
    // text: "Ingin Menghapus Data Ini ?",
    html:
      "Apakah Anda Yakin Ingin " +
      "Menghapus " +
      "<h1><b>Kode Transaksi " +
      params.kode_transaksi +
      "</b> ?</h1>",
    icon: "warning",
    position: "top-center",
    cancelButtonText: "Tidak",
    showCancelButton: true,
    confirmButtonText: "OK",
    showConfirmButton: true,
  }).then(async (result) => {
    if (result.isConfirmed) {
      try {
        for (const data of listToko) {
          await putParameterHarga(
            data.portal + "/api/v1/paratransaksi/delete/by-kode-transaksi",
            { kode_transaksi: params.kode_transaksi }
          );
        }
        NotifSucces("Data Berhasil Di Hapus");
      } catch (error) {
        NotifError("Data Gagal Di Hapus");
      }
      deleteData("paratransaksi/delete/by-kode-transaksi/" + params._id)
        .then(() => {
          NotifSucces("Data Berhasil Di Hapus").then(() =>
            dispatch(getParamterTransaksi())
          );
        })
        .catch((err) => {
          ToastNotification("error", err.response.data);
        });
    }
  });
};
class MasterKodeTransaksi extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isEdit: false,
      modalDialog: false,
      isLoading: false,
      columns: [
        {
          dataField: "kode_transaksi",
          text: "Kode Transaksi ",
          sort: true,
        },
        {
          dataField: "action",
          text: "Action",
          csvExport: false,
          headerClasses: "text-center",
          formatter: (rowcontent, row) => {
            let dataEdit = {
              id: row._id,
              kode_transaksi: row.kode_transaksi,
              kode_transaksi_lama: row.kode_transaksi_lama,
            };

            return (
              <div className="row text-center">
                <div className="col-12">
                  <button
                    onClick={() => this.FormEditKodeTransaksi(dataEdit)}
                    className="btn btn-primary mr-3"
                  >
                    <i className="fa fa-edit"></i>
                  </button>
                  <button
                    onClick={() =>
                      HapusDataKodeTransaksi(
                        row,
                        this.props.dispatch,
                        this.props.getMasterToko
                      )
                    }
                    className="btn btn-danger"
                  >
                    <i className="fa fa-trash"></i>
                  </button>
                </div>
              </div>
            );
          },
        },
      ],
      dataKodeTransaksi: [
        {
          kode_transaksi: "Uang Makan",
        },
      ],
    };
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  componentDidMount() {
    this.props.dispatch(getParamterTransaksi());
    this.props.dispatch(getMasterToko());
  }

  async handleSubmit(data) {
    this.setState({
      isLoading: true,
    });
    let data_edit = {
      kode_transaksi_lama: data.kode_transaksi_lama,
      kode_transaksi: data.kode_transaksi,
    };
    let data_post = {
      id: data.id,
      kode_transaksi: data.kode_transaksi,
    };
    let listToko = this.props.getMasterToko;
    if (this.state.isEdit) {
      try {
        for (const data of listToko) {
          await putParameterHarga(
            data.portal + "/api/v1/paratransaksi/edit/by-kode-transaksi",
            data_edit
          );
        }
        ToastNotification("success", "Data Berhasil Diedit");
      } catch (error) {
        ToastNotification("error", "Data Gagal Diedit");
      }
      putData("paratransaksi/edit/by-kode-transaksi/" + data.id, data_edit)
        .then(() => {
          this.setState({
            isLoading: false,
          });
          this.props.dispatch(hideModal());
        })
        .then(() => ToastNotification("success", "Data Berhasil Diedit"))
        .then(() => {
          this.props.dispatch(getParamterTransaksi());
        })
        .then(() => {
          this.setState({ isLoading: false });
        })
        .then(() => {
          this.props.dispatch(reset("ModalKodeTrransaksi"));
        })
        .catch((error) => {
          ToastNotification(
            "info",
            error.response === undefined
              ? "Terjadi Kesalahan Saat Megirim Data "
              : error.response.data
          );
          this.setState({ isLoading: false });
        });
    } else {
      try {
        for (const data of listToko) {
          await postParameterHarga(
            data.portal + "/api/v1/paratransaksi/add",
            data_post
          );
        }
        ToastNotification("success", "Data Berhasil Disimpan");
      } catch (error) {
        ToastNotification("error", "Data Gagal Disimpan");
      }
      postData("paratransaksi/add", data_post)
        .then(() => {
          this.setState({
            isLoading: false,
          });
          this.props.dispatch(hideModal());
        })
        .then(() => {
          ToastNotification("success", "Data Berhasil Disimpan");
        })
        .then(() => {
          this.props.dispatch(getParamterTransaksi());
        })
        .then(() => {
          this.props.dispatch(reset("ModalKodeTrransaksi"));
        })
        .then(() => {
          this.setState({ isLoading: false });
        })
        .catch((error) =>
          ErrorHandling(
            error,
            "paratransaksi/reactive/by-kode-transaksi/",
            // data.kode_transaksi,
            data_post,
            this.props.dispatch,
            getParamterTransaksi(),
            hideModal(),
            "ModalKodeTrransaksi"
          ).then(() => {
            this.setState({ isLoading: false });
          })
        );
    }
  }

  FormEditKodeTransaksi(dataEdit) {
    this.props.dispatch(showModal());
    this.props.dispatch(ShowModalKodeTransaksi(dataEdit));
    this.setState({
      isEdit: true,
    });
  }
  showModalKodeTranaksi() {
    this.props.dispatch(showModal());
    this.props.dispatch(ShowModalKodeTransaksi(false));
    this.setState({
      isEdit: false,
      isLoading: false,
    });
  }
  render() {
    return (
      <div>
        <ol className="breadcrumb float-xl-right">
          <li className="breadcrumb-item">
            <Link to="#">Data Master</Link>
          </li>
          <li className="breadcrumb-item active">Master Kode Transaksi</li>
        </ol>
        <h1 className="page-header">Master Kode Transaksi </h1>
        <Panel>
          <PanelHeader>Master Kode Transaksi</PanelHeader>
          <br />

          {/* Master Kode Transaksi */}
          <div className="container">
            <Tabel
              keyField="kode_transaksi"
              tambahData={true}
              handleClick={() => this.showModalKodeTranaksi()}
              exportCsv={true}
              data={this.props.DataTransaksi}
              columns={this.state.columns}
              empty={this.props.DataTransaksi}
              textEmpty="Data Jenis Kosong"
            />
            {/* <ToolkitProvider
              keyField="kode_transaksi"
              data={this.props.DataTransaksi || []}
              columns={this.state.columns}
              search
              exportCSV={{
                fileName: "Export Master Kondisi Transaksi.csv",
              }}
            >
              {(props) => (
                <div className="row">
                  <div className="col-6">
                    <button
                      onClick={() => this.showModalKodeTranaksi()}
                      className="btn btn-primary"
                    >
                      {" "}
                      <i className="fa fa-plus"></i>{" "}
                    </button>
                  </div>
                  <div className="col-6">
                    <div className="text-right">
                      <SearchBar {...props.searchProps} />
                    </div>
                  </div>
                  <hr />
                  <div className="col-12">
                    <BootstrapTable
                      pagination={paginationFactory()}
                      {...props.baseProps}
                    />
                    <br />
                    <ExportCSVButton {...props.csvProps}>
                      Export CSV!!
                    </ExportCSVButton>
                  </div>
                </div>
              )}
            </ToolkitProvider> */}
          </div>
          <br />
          {/* End Master Kode Transaksi */}
          <ModalGlobal
            title={
              this.state.isEdit
                ? "Edit Data Kode Transaksi"
                : "Tambah Data Kode Transaksi"
            }
            size="P"
            content={
              <FormModalKodeTransaksi
                isLoading={this.state.isLoading}
                onSubmit={(data) => this.handleSubmit(data)}
              />
            }
          />

          {/* End Tambah Master Kode Transaksi  */}
        </Panel>
      </div>
    );
  }
}

export default connect(maptostate, null)(MasterKodeTransaksi);
